.news-item {
    display: flex;
    gap: 1.5rem;
    padding: 1rem;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    background-color: $light-color;

    img,
    .news-placeholder {
        width: 250px;
        height: 250px;
        object-fit: cover;
        aspect-ratio: 1/1;

    }

    .news-placeholder {
        display: flex;
        align-items: center;
        justify-content: center;

        .placeholder-icon {
            font-size: 100px
        }
    }

    .news-item-right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .news-item-date {
            font-style: italic;
        }

        .news-item-title {
            margin-top: 0.7rem;
            font-size: 1.5rem;

            a {
                text-decoration: none;
                color: $secondary-color-darken;
            }
        }

        .news-item-description {
            margin-top: 0.5rem;
            font-size: 1.2rem;
        }

        .news-item-button {
            margin-bottom: 1rem;
        }
    }
}