.admin-page {

    padding: 6rem;

    .with-line {
        color: $secondary-color;
    }

    .with-line::after {
        background-color: $secondary-color;
    }

    .admin-description {
        margin: 4rem 0;
        font-size: 1.4rem;
        color: $dark-color;
        text-align: center;
    }


    .tiles-container {
        display: flex;
        gap: 2rem;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 2rem;
    }

}